import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Spinner } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';

import StationList from '../../containers/organisms/StationList';
import styles from './StationSelector.module.css';

class StationSelector extends React.Component {
  componentWillMount() {
    const { city, load } = this.props;
    load(city);
  }

  render() {
    const { id, onBack, isLoading } = this.props;
    return (
      <Panel id={id}>
        <PanelHeader
          left={
            <HeaderButton onClick={onBack}>
              <Icon28ChevronBack />
            </HeaderButton>
          }
        >
          Метро
        </PanelHeader>
        {isLoading ? (
          <div className={styles.Spinner}>
            <Spinner size="large" />
          </div>
        ) : (
          <StationList />
        )}
      </Panel>
    );
  }
}

StationSelector.propTypes = {
  id: PropTypes.string.isRequired,
  onBack: PropTypes.func.isRequired,
  isLoading: PropTypes.bool.isRequired,
  city: PropTypes.string.isRequired,
  load: PropTypes.func.isRequired
};

export default StationSelector;
