import { connect } from 'react-redux';

import actions from '../../actions';
import { getSearchParams } from '../../selectors';
import StationSelector from '../../components/panels/StationSelector';

const mapStateToProps = (state, ownProps) => ({
  ...ownProps,
  isLoading: state.entities.stations.isLoading,
  city: getSearchParams(state).city.Id
});

const mapDispatchToProps = dispatch => ({
  load: city => dispatch({ type: actions.LOAD_STATIONS, payload: { city } })
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(StationSelector);
