import React from 'react';
import PropTypes from 'prop-types';
import { Panel, Header, Spinner } from '@vkontakte/vkui';

import styles from './Home.module.css';
import SearchForm from '../../containers/organisms/SearchForm';

const Home = ({ id, isInitialized }) => (
  <Panel id={id} theme="white">
    <div className={styles.Header}>
      <Header>Найдите проверенного врача и запишитесь на прием</Header>
    </div>
    {isInitialized ? <SearchForm /> : <Spinner size="large" />}
  </Panel>
);

Home.propTypes = {
  id: PropTypes.string.isRequired,
  isInitialized: PropTypes.bool.isRequired
};

export default Home;
