import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import { Field, reduxForm } from 'redux-form';
import { FormLayout, Button } from '@vkontakte/vkui';
import { ReactComponent as Icon14Statoscope } from '../../svg/Icon14Statoscope.svg';
import { ReactComponent as Icon14Pin } from '../../svg/Icon14Pin.svg';
import { ReactComponent as Icon14Calendar } from '../../svg/Icon14Calendar.svg';
import { ReactComponent as Icon14Subway } from '../../svg/Icon14Subway.svg';

import SearchField from '../moleculs/SearchField';
import './SearchForm.css';

const SearchForm = ({
  handleSubmit,
  selectCity,
  selectSpeciality,
  selectStation,
  withSubway
}) => (
  <FormLayout className="SearchForm" onSubmit={handleSubmit}>
    <Field
      name="speciality"
      component={SearchField}
      placeholder="Кого?"
      onClick={selectSpeciality}
      icon={<Icon14Statoscope />}
      format={spec => spec && spec.Name}
      readOnly
    />
    <Field
      name="city"
      component={SearchField}
      placeholder="Где?"
      onClick={selectCity}
      icon={<Icon14Pin />}
      format={city => city && city.Name}
      readOnly
    />
    {withSubway && (
      <Field
        name="station"
        component={SearchField}
        placeholder="Метро"
        onClick={selectStation}
        icon={<Icon14Subway />}
        format={station => station && station.Name}
        readOnly
      />
    )}
    <Field
      name="date"
      component={SearchField}
      placeholder="Когда?"
      icon={<Icon14Calendar />}
      format={date =>
        moment(date)
          .locale('ru')
          .calendar(null, {
            sameDay: '[Сегодня]',
            nextDay: '[Завтра]',
            nextWeek: 'Do MMMM',
            sameElse: 'Do MMMM'
          })
      }
      readOnly
    />
    <Button size="xl">Найти врача</Button>
  </FormLayout>
);

SearchForm.propTypes = {
  handleSubmit: PropTypes.func.isRequired,
  selectCity: PropTypes.func.isRequired,
  selectSpeciality: PropTypes.func.isRequired,
  selectStation: PropTypes.func.isRequired,
  withSubway: PropTypes.bool.isRequired
};

export default reduxForm({
  form: 'search',
  destroyOnUnmount: false
})(SearchForm);
