import { connect } from 'react-redux';

import SearchForm from '../../components/organisms/SearchForm';
import actions from '../../actions';
import { getSearchParams } from '../../selectors';

const mapStateToProps = state => {
  const { city } = getSearchParams(state);
  return {
    withSubway: [2, 3].includes(city.SearchType)
  };
};

const mapDispatchToProps = dispatch => ({
  onSubmit: query => {
    dispatch({
      type: actions.SEARCH,
      payload: {
        city: query.city.Id,
        speciality: query.speciality.Id,
        when: query.date,
        subwayStation: query.station && query.station.Id,
        page: 0
      }
    });
    dispatch({ type: actions.SHOW_SEARCH_RESULTS });
  },
  selectCity: () => dispatch({ type: actions.SHOW_CITY_SELECTOR }),
  selectSpeciality: () => dispatch({ type: actions.SHOW_SPECIALITY_SELECTOR }),
  selectStation: () => dispatch({ type: actions.SHOW_STATION_SELECTOR })
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SearchForm);
